<template>
  <div class="text-sm md:text-2xl text-white">        
    <span class="rounded-l-lg bg-gray-600 p-1">{{years}}</span><span class="rounded-r-lg bg-gray-800 p-1 mr-2">years</span> 
    <span class="rounded-l-lg bg-gray-600 p-1">{{days}}</span><span class="rounded-r-lg bg-gray-800 p-1 mr-2">days</span> 
    <span class="rounded-l-lg bg-gray-600 p-1">{{hours}}</span><span class="rounded-r-lg bg-gray-800 p-1 mr-2">hours</span> 
    <span class="rounded-l-lg bg-gray-600 p-1">{{mins}}</span><span class="rounded-r-lg bg-gray-800 p-1 mr-2">minutes</span> 
    <span class="rounded-l-lg bg-gray-600 p-1">{{secs}}</span><span class="rounded-r-lg bg-gray-800 p-1 mr-2">seconds</span>     
  </div>
</template>

<script>
export default {
  name: 'CountUp',
  data () {
    return {
      years: 0,
      days: 0,
      hours: 0,
      mins: 0,
      secs: 0,
      countUpInterval: null
    }
  },
  methods: {
    count () {
      let countFrom = new Date('Aug 6, 2019 00:00:00').getTime()
      const now = new Date('Aug 25, 2022 15:49:13')
      countFrom = new Date(countFrom)
      const timeDifference = (now - countFrom)
        
      const secondsInADay = 60 * 60 * 1000 * 24
      const secondsInAHour = 60 * 60 * 1000
        
      this.days = Math.floor(timeDifference / (secondsInADay) * 1)
      this.years = Math.floor(this.days / 365);
      if (this.years > 1){ this.days -= (this.years * 365) }

      this.hours = String(Math.floor((timeDifference % (secondsInADay)) / (secondsInAHour) * 1)).padStart(2, '0')
      this.mins = String(Math.floor(((timeDifference % (secondsInADay)) % (secondsInAHour)) / (60 * 1000) * 1)).padStart(2, '0')
      this.secs = String(Math.floor((((timeDifference % (secondsInADay)) % (secondsInAHour)) % (60 * 1000)) / 1000 * 1)).padStart(2, '0')
    }
  },
  created() {
    this.count()    
  }
}
</script>